            justify-content: center;
    max-width: 80%;
}
.showBlogs h1 {
    font-size: 2.5rem;
    font-weight: bold;
    color: #FF8E23;
}
.showBlogs .search {
    height: 30px;
    border: 2px solid #22BAD9;
    padding-left: 10px;
    border-radius: 5px;
}
