}
/* 添加博客页面 */
.addBlog, .modify-blog {
    margin: 10px auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: stretch    ;
        -ms-flex-align: stretch    ;
            align-items: stretch    ;
}
.addBlog .editor, .modify-blog .editor{
    padding: 0 10px;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.addBlog h2, .modify-blog h2{
    width: 100%;
    font-size: 1.8rem;
    color: #FF8E23;
}
.addBlog .editor .checkDiv, .modify-blog .editor .checkDiv {
    display: inline-block;
}
.addBlog .editor button, .modify-blog .editor button {
    outline: none;
    width: 70px;
    height: 40px;
    background-color: #FF8E23;
    color: #fff;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
}
.addBlog .editor input[type=text], textarea {
    width: 100%;
    margin: 10px auto;
    height: 30px;
    border: 1px solid #22BAD9;
    padding-left: 10px;
    border-radius: 5px;
}
.modify-blog .editor input[type=text], textarea {
    width: 100%;
    margin: 10px auto;
    height: 30px;
    border: 1px solid #22BAD9;
    padding-left: 10px;
    border-radius: 5px;
}
.addBlog .editor textarea, .modify-blog .editor textarea {
    min-width: 500px;
    height: 400px;
    font-size: 1.2rem;
}
.checkDiv {
    margin-left: 10px;
}
.addBlog .editor select, .modify-blog .editor select {
    margin: 10px auto;
    width: 100%;
    border: 1px solid #22BAD9;
    height: 30px;
    outline: none;
    border-radius: none;
}
.view {
    overflow: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background-color: rgb(240, 240, 240);
    padding: 10px;
    border: 1px solid #22BAD9;
    border-radius: 10px;
    margin-top: 10px;
}
/* 响应式 屏幕小于700px*/
@media screen and (max-device-width: 700px) {
    /* * {
        outline: 1px solid red;
    } */
    html {
        font-size: 20px;
    }
    img {
        max-width: 100%;
    }
    .index .index-img {
        width: 50vw;
    }
    #singleBlog h2 {
        font-size: 1rem;
    }
    #singleBlog p {
        display: none;
    }
    .addBlog {
        max-width: 90vw;
    }
    .addBlog .editor textarea, .modify-blog .editor textarea{
        min-width: 1px;
        width: 100%;
        height: 400px;
        font-size: 1.2rem;
    }
    .modify-blog {
        max-width: 90vw;
    }
    .addBlog .editor button, .modify-blog .editor button {
        width: 100%;
    }
    .this-blog {
        max-width: 90vw;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -ms-flex-wrap: nowrap;
            flex-wrap: nowrap;
    }
    .markedHtml {
        max-width: 90vw;
        text-overflow: ellipsis;
    }
    .modify button {
        width: 100%;
        margin-top: 10px;
    }
}